ul[class],
ol[class] {
  list-style: none;
  padding: 0;
  margin: 0;
}





body {
  margin: 0;
  font-family:'Inter', system-ui;
  background-color: #E7F6FF;

  a {
    text-decoration:none;
    color: black;
  }
}


.wrapper{
  max-width: 1080px;
  background-color: white;
  border-radius: 20px;
  margin: 50px auto;
  box-shadow: 0 10px 20px rgba(0,0,0,0.04);

}

header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px;
  border-bottom: 1px solid #eaeaea;

  .headerRight{
    display: flex;

    li{
      margin-right: 20px;
      cursor: pointer;
    }

    img{
      margin-right: 15px;

    }
  }

  .headerLeft{
    display: flex;
    align-items: center;

    h2{
      margin: 0;
    }

    img{
      margin-right: 18px;
    }

    p{
      margin: 0;
      color: #9D9D9D;
    ;
    }

    h3{
      margin: 0;
      text-transform: uppercase;
    }
  }
}



.content{
  padding: 40px;

  .search-block{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;


    .search{
      border: 1px solid #f3f3f3;
      height: 45px;
      width: 250px;
      border-radius: 10px;
      padding: 0 15px;
      position: relative;

    .removeBtn{
      opacity: 0.5;
      top: 14px;
      position: absolute;
      cursor: pointer;
      right: 10px;
    }

      input{
        border: 0;
        padding: 13px;
        font-size: 16px;
        outline: none;
        width: 200px;
      }

    }
  }



  .sneakers{
    display: flex;
    flex-wrap: wrap;
  }

  .images{
    width: 133px;
    height: 112px;
    display: block;
    margin: 0 auto;
  }



}


.overlay{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.1);
  z-index: 1;



  .drawer{
    right: 0;
    height: 890px;
    position: absolute;
    width: 420px;
    background: white;
    box-shadow: -10px 4px 24px rgba(0,0,0,0.1);
    padding: 30px;
    display: flex;
    flex-direction: column;

    .cartEmpty{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      text-align: center;
      flex: 1;




      .greenButton{
        margin-top: 20px;

        &:hover{
          img{
            transform: rotate(180deg) translateX(3px);
          }
        }
      }

      .Empty{
        margin-bottom: 20px;
        width: 120px;
      }

      p{
        opacity: 0.6;
        width: 280px;
        line-height: 24px;
      }
    }

    h2{
      margin: 0;
      font-size: 24px;
      padding-bottom: 30px;
      display: flex;
      justify-content: space-between;

      img{
        cursor: pointer;
      }
    }

    .items{
      flex: 1;



      .cartItem{
        display: flex;
        align-items: center;
        border: 1px solid #F3F3F3;
        border-radius: 20px;
        overflow: hidden;
        margin-bottom: 20px;

        .cardInfo{
          margin: 30px;

          p{
            margin-bottom: 5px;
            font-size: 16px;
          }

          b{
            font-size: 14px;
          }


        }
        img{
          padding: 10px;
        }
        .removeBtn{
          opacity: 0.5;
          cursor: pointer;
          transition: opacity 0.15s ease-in-out;

          &:hover{
            opacity: 1;
          }
        }

      }
    }
    .cartTotalBlock{
      position: absolute;
      bottom: 0;
      margin-bottom: 20px;
      div{
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #DFDFDF;
        position: relative;
        top: -4px;
        margin: 0 10px;
      }

      li{
        display: flex;
        align-items: flex-end;
        margin-bottom: 20px;
      }
    }

    .greenButton{
      width: 420px;
      height: 55px;
      border-radius: 18px;
      background: #9DD558;
      color: white;
      border: 0;
      font-size: 16px;
      cursor: pointer;
      margin-top: 20px;
      transition: background 0.1s ease-in-out;
      position: relative;

      &:disabled{
        background-color: #bebebe;
        cursor: default ;
      }


      &:hover{
        background: lighten(#9DD558, 7%);
      }

      &:active{
        background: darken(#9DD558,5%);
      }


      &:hover{
        img{
          transform: translateX(5px);
        }
      }

      img{
        position: absolute;
        right: 30px;
        top: 20px;
        transition: transform 0.15s ease-in-out;

      }
    }



  }
}






























